<script>
    import { getQueryParam, throttle ,InitBridge, setupWebViewJavascriptBridge} from '@/utils/common';
    import { fetch } from '@/utils/fetch';
    import Swiper from 'swiper/dist/js/swiper.min';
    import '@/assets/styles/swiper.css';
    import '@/assets/styles/common';
    
    export default {
        data(){
            return {
                shareData: {
                    title: '',
                    desc: '',
                    url: ''
                },
                isShowFooter: !0,
                isShowBullet: !1,
                bulletInterval: 1,
                bulletList: [],
                timer: null,
                afterTimer: null,
                bgSrc:'',
                isAndroid: null,
            }
        },
        methods: {
            //
            callAppShare(){
                if (this.isAndroid) {
                    InitBridge((bridge) => {
                        bridge.showSharedDialogForShareStore();
                    }); 
                }else{
                    setupWebViewJavascriptBridge((bridge) => {
                        bridge.callHandler('showSharedStoreMethod',()=>{})
                    }); 
                }
            },
            afterEnter(){
                this.afterTimer = setTimeout(() => {
                    this.isShowBullet = !1;
                }, 4000);
            },
            afterLeave(){
                this.bulletList.shift();
                if(this.bulletList.length<=0 || this.paused){
                    return;
                }
                this.showBullet();
            },
            showBullet(){
                if(this.timer){
                    clearTimeout(this.timer);
                }
                this.timer = setTimeout(() => {
                    this.isShowBullet = !0;
                }, this.bulletInterval * 1000);
            },
            pullBulletData(){
                fetch(`/message/m/invite`).then(res => {
                    if(res.code !== 1){
                        return;
                    }
                    this.bulletList = res.data.msglist;
                    this.bulletInterval = res.data.frequency;
                    if (this.bulletList.length > 0) {
                        this.showBullet();
                    }
                }).catch(err => {
                    console.log(err);
                });
            },
            /* 
            initSwiper(){
                var that = this
                var swiper = new Swiper('.swiper-container', {
                    slidesPerView: 'auto',
                    spaceBetween: 30,
                    centeredSlides: true,
                    pagination: {
                        el: '.swiper-pagination',
                        clickable: !1,
                    },
                    loop: !0,
                    loopedSlides: 3,
                    observer:true,
                    on: {
                        //swiper
                        slideChangeTransitionEnd: function(){
                            if(this.activeIndex == 0 || this.activeIndex == 3 || this.activeIndex == 6){
                                that.bgSrc = require('../../../assets/images/introduce/bg-6.png')
                            }else if(this.activeIndex == 1 || this.activeIndex == 4 || this.activeIndex == 7){
                                that.bgSrc = require('../../../assets/images/introduce/bg-7.png')
                            }else if(this.activeIndex == 2 || this.activeIndex == 5 || this.activeIndex == 8){
                                that.bgSrc = require('../../../assets/images/introduce/bg-3.png')
                            }
                        },
                    },
                });
            },
            
            initScrollHandle(){
                //let topBtnPos = this.$refs.topBtn.getBoundingClientRect();
                let btmBtnPos = this.$refs.btmBtn.getBoundingClientRect();
                //let minH = topBtnPos.bottom + window.pageYOffset; 
              
                window.addEventListener('scroll', throttle(() => {
                    let scrollY = window.pageYOffset;
                    let maxH = getOffsetTop(this.$refs.btmBtn)-window.innerHeight; 
                    
                    if(scrollY > 2300){
                        this.isShowFooter = !1;
                    }else{
                        this.isShowFooter = !0;
                    }
                }, 100, 200), !1);

                function getOffsetTop(el) {
                    return el.offsetParent ?
                        el.offsetTop + getOffsetTop(el.offsetParent) :
                        el.offsetTop
                }
            },
            */
            init(){
                //this.initSwiper();
                this.pullBulletData();
                this.isAndroid = window.navigator.userAgent.toLowerCase().indexOf('android') > -1;
            }
        },
        mounted(){
            this.init();
        }
    }
</script>
