        position:absolute;
        top:0;
        left:48%;
    }

    .profit-arrow img{
        display:block;
        width:100%;
        height:100%;
    }

    .intro-five{
        margin-top: 0.49rem;
        padding-bottom: 0.47rem;
        .it--big{
            color: #000;
        }

        .intro__swiper{
            margin-top: 0.42rem;
        }

        .card{
            display: flex;
            flex-direction: column;
            align-items: center;
            img{
                display: block;
                width: 1.16rem;
                height: 1.16rem;
                position:absolute;
                z-index:3;
                top:0.15rem;
                left:0.15rem;
            }
            
            .card-shopname{
                line-height: 0.33rem;
                color: #333;
                font-size: 0.24rem;
                margin-bottom:0.22rem;
                margin-top:0.25rem;
                margin-left:0.18rem;
            }
            

            .card-other{
                line-height: 0.3rem;
                color: #7f7f7f;
                font-size: 0.22rem;
                margin-left:0.18rem;

                .card-other-span{
                    color:#ccc;
                    margin:0.4rem 0;
                }
            }
        }
    }

    .card-money{
        width:2.04rem;
        height:0.51rem;
        line-height: 0.4rem;
        position:absolute;
        z-index:4;
        top:0.97rem;
        left:-0.3rem;
        text-align:center;
        color: #fff;
        font-size: 0.30rem;
    }
    .style1{
        background: url(../../../assets/images/understand/understand_decoration1.png) center 0 / contain no-repeat;
    }
    .style2{
        background: url(../../../assets/images/understand/understand_decoration2.png) center 0 / contain no-repeat;
    }
    .style3{
        background: url(../../../assets/images/understand/understand_decoration3.png) center 0 / contain no-repeat;
    }
   

    .intro-last{
        position: relative;
        padding-bottom: 2.12rem;
        .app-img{
            img{
                display: block;
                width: 5.48rem;
                height: auto;
                margin: 0 auto;
            }
        }
        .app-text{
            background: url(../../../assets/images/understand/understand_icon_bottom.png) center 0 / contain no-repeat;
            margin-top:1.26rem;
            margin-bottom:0.39rem;
        }
        .btn-openshop{
            width:4.02rem;
            height:0.96rem;
            font-size:0.36rem;
            color:rgba(255,255,255,1);
            line-height:0.96rem;
            text-align:center;
            background:linear-gradient(189deg,rgba(251,80,48,1),rgba(253,101,5,1));
            border-radius:0.48rem;
            box-shadow:0 0.10rem 0.14rem rgba(252,92,22,0.35);
            margin-left:1.46rem;
            margin-top:0.8rem;
        }
        .app-arrow img{
            width:0.3rem;
            height:0.14rem;
            margin-left:48%;
            display:block;
        }
        .app-differ{
            position: absolute;
            left: 0;
            top: 4.1rem;
            width: 100%;
            height: 15rem;
            box-sizing: border-box;
            background-color: #F3DAAD;
            background-size:100%;
            color:#333;
            .differ-title{
                font-size: 0.42rem;
                color: #333;
                line-height: 0.67rem;
                text-align: center;
                margin-bottom: 0.6rem;
                font-family: 'PingFangSC-Light',"Helvetica Neue",Helvetica,STHeiTi,sans-serif;
                margin-top:0.9rem;
            }

            .differ-list{
                font-size: 0.3rem;
                color: #333;
                line-height: 0.42rem;
                margin-bottom: 0.28rem;
                position:relative;
                margin-left:0.86rem;
                font-weight:bold;

                &:before{
                    content:'';
                    position: absolute;
                    top: 0.07rem;
                    left: -0.4rem;
                    width: 0.22rem;
                    height: 0.25rem;
                    background: url(../../../assets/images/introduce/flag.png) 0 0/contain no-repeat;
                }
            }

            .differ-desc{
                padding: 0.08rem 0.46rem 0.78rem;
                font-size: 0.26rem;
                color: #333333;
                line-height: 0.44rem;
            }
        }
        .app-logo-bg{
            width:1.64rem;
            height:1.64rem;
            background:rgba(255,255,255,1);
            border-radius:50%;
            box-shadow:0.02rem 0 0.24rem rgba(252,87,33,0.2);
            position: absolute;
            left: 2.6rem;
            top: -0.8rem;
        }

        .app-welcome{
            position: absolute;
            left: 0.3rem;
            top: 17.5rem;
            width:6.90rem;
            height:7.22rem;
            background:rgba(255,255,255,1);
            border-radius:0.10rem;
            box-shadow:0 0 0.1rem rgba(81,80,80,0.16);
            margin-bottom:0.5rem;
            
            .app-logo{
                width: 1rem;
                height: 1.17rem;
                position: absolute;
                left: 2.9rem;
                top: -0.6rem;

                img{
                    display: block;
                    width: 100%;
                    height: 100%;
                }
            }

            .app-slogan{
                font-size: 0.48rem;
                color: #333;
                line-height: 0.62rem;
                text-align: center;
                margin-bottom: 0.66rem;
            }
            .app-slogan-small{
                font-size: 0.32rem;
                color: #333;
                line-height: 0.4rem;
                text-align: center;
                margin-bottom: 0.75rem;
            }

            .app-desc{
                text-align:center;
                font-size: 0.28rem;
                color: #787878;
                line-height: 0.1rem;
                margin-bottom: 0.39rem;
            }
        }
    }

    .float-footer{
        position: fixed;
        bottom: 0;
        left: 0;
        z-index: 100;

        display: flex;
        
        width: 100%;
        height: 1.0rem;
        line-height: 1.0rem;
        background-color: #fff;
        box-shadow:0px 1px 13px 0px rgba(202,200,198,0.5);

        .ff-lf{
            flex: 1;
            font-size: 0.28rem;
            color: #333;
            padding-left: 0.32rem;
        }

        .ff-rt{
            width: 2.52rem;
            background-image: linear-gradient(90deg, #FF8C00, #FF652B);
            font-size: 0.32rem;
            color: #fff;
            text-align: center;
        }
    }

    .bullet-screen{
        position: fixed;
        left: 0.32rem;
        top: 0.6rem;
        height: 0.54rem;
        line-height: 0.56rem;
        max-width: 4rem;
        padding: 0 0.3rem;
        border-radius: 0.06rem;
        color: #fff;
        font-size: 0.24rem;
        text-align: center;
        background-image: linear-gradient(90deg, #FF8C00 0, #FF5400 100% );
        z-index: 2000;
        box-sizing: border-box;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        opacity: 0.7;
    }

    .bullet-enter-active{
        opacity: 0.7;
        transform: translate3d(0, 0, 0);
        transition: all .5s ease-in-out;
    }
    .bullet-enter{
        opacity: 0;
        transform: translate3d(0, 150%, 0);
    }

    .bullet-leave-active {
        transition: all .8s cubic-bezier(1.0, 0.5, 0.8, 1.0);
    }

    .bullet-leave-to{
        opacity: 0;
        transform: translate3d(0, -100%, 0);
    }

</style>




